import React, { Component } from "react";
import { inject, observer } from "mobx-react";
import AccountHeader from "../../components/partial/AccountHeader";
import ShareIcon from "../../assets/images/fenxiang.png";
import ShareImg from "../../components/partial/ShareImg";
import fzIcon from "../../assets/images/fuzhi.png";
import wxIcon from "../../assets/images/weixin.png";
import zfbIcon from "../../assets/images/zhifubao.png";
import pingpp from "pingpp-js";
import "./swiper.scss";
import "./Fangwei.scss";
// import Circle from 'sanyuelanv-circle'
import { Button, Toast, Tabs, Modal, Icon } from "antd-mobile";
import { CommodityApi } from "../../api";
import {
  cryptoAccountDisplay,
  formatWalletPrice,
  formatZeroNumber,
  getQueryString,
  // iosTrouchFn,
  formatDate,
  formatTags,
} from "../../utils/format";
import { isIosOrAndroid, isWeixn } from "../../utils/common";

// console.log(walletData)
@inject("userM4DStore")
@inject("localeStore")
@observer
class Fangwei extends Component {
  state = {
    copyValue: "", //
    modal: false,
    modal2: false, //
    payType: "wx", //

    cpData: {},
    bank_name: "", //
    bank_user: "", //
    bank_number: "", //
    sell_price: "", //
    code_img: "",
    isSubmit: true,
    base64Img: "", //base64
    bankList: [],
    chooseIndex: 0, //
    Zhongduan: 0, //
  };
  componentDidMount() {
    let _this = this;
    const { userM4DStore, history } = this.props;
    const {
      localeStore: {
        locale: { WALLET, ADD },
        lang,
      },
      localeStore,
    } = this.props;

    userM4DStore.getUserStatus();
    // console.log(getQueryString("id"));
    // console.log(formatZeroNumber(0.15));
    this.commodityview();
    this.getBankList();
    let zd = isIosOrAndroid();

    this.setState({
      Zhongduan: zd, //1 ios2
    });
    // console.log(window.location);
  }
  componentWillUnmount() {
    window.onresize = null;
  }

  componentWillUnmount() {
    // window.onresize = null
  }

  getBase64 = (imgUrl) => {
    let _this = this;
    window.URL = window.URL || window.webkitURL;
    let xhr = new XMLHttpRequest();
    xhr.open("get", imgUrl, true);
    // 
    xhr.responseType = "blob";
    xhr.onload = function () {
      if (this.status == 200) {
        //blob
        let blob = this.response;
        // console.log("blob", blob);
        // 
        let oFileReader = new FileReader();
        oFileReader.onloadend = function (e) {
          //  base64
          let base64 = e.target.result;
          // console.log("", base64);
          _this.setState({ base64Img: base64 });
        };
        oFileReader.readAsDataURL(blob);
      }
    };
    xhr.send();
  };

  onInputChange = (e, key) => {
    const { value } = e.target;
    this.setState({ [key]: value });
  };
  onAmountChange = (e) => {
    const { value } = e.target;
    const reg = /^\d+(\.)?\d{0,2}?$/;
    if (value && !reg.test(e.target.value)) {
      return;
    }
    this.setState({ sell_price: value });
  };
  // 
  commodityview = () => {
    let _this = this;
    const {} = this.state;
    const { userM4DStore } = this.props;
    CommodityApi.commodityview({
      open_id: userM4DStore.openid,
      p_id: getQueryString("id"),
    }).then((res) => {
      if (res.status !== 1) {
        Toast.info(res.msg);
        return;
      }
      // console.log(res.data);
      this.setState(
        {
          cpData: res.data,
          bank_name: res.data.bankName, //
          bank_user: res.data.bankUser, //
          bank_number: res.data.bankNumber, //
          sell_price:
            (Number(res.data.sellPrice) !== 0 && res.data.sellPrice) || "", //
          // sell_price: '', //
        },
        () => {
          _this.getBase64(res.data.pFace);
        }
      );
    });
  };
  // 
  sellcommodity = () => {
    let _this = this;
    const {
      bank_name, //
      bank_user, //
      bank_number, //
      sell_price, //
      bank_type,
      code_img,
      cpData,
    } = this.state;
    if (sell_price > Number(cpData.sellPriceMax)) {
      Toast.info("" + cpData.sellPriceMax);
      return;
    }
    if (sell_price < Number(cpData.sellPriceMin)) {
      Toast.info("" + cpData.sellPriceMin);
      return;
    }
    this.setState({ isSubmit: false });

    const { userM4DStore } = this.props;
    CommodityApi.sellcommodity({
      open_id: userM4DStore.openid,
      p_id: getQueryString("id"),
      bank_name: bank_name,
      bank_user: bank_user,
      bank_number: bank_number,
      sell_price: sell_price,
      code_img: code_img,
      bank_type: bank_type,
    }).then((res) => {
      this.setState({ isSubmit: true });

      if (res.status !== 1) {
        Toast.info(res.msg, 2, () => {
          _this.setState({
            modal: false,
            modal2: false,
          });
        });
        return;
      }

      Toast.info("", 2, () => {
        _this.commodityview();
        _this.pingppH5();
        _this.setState({
          modal: false,
          modal2: false,
        });
      });
    });
  };
  // 
  sellcancel = () => {
    let _this = this;
    const {} = this.state;
    this.setState({ isSubmit: false });

    const { userM4DStore } = this.props;
    CommodityApi.sellcancel({
      open_id: userM4DStore.openid,
      p_id: getQueryString("id"),
    }).then((res) => {
      if (res.status !== 1) {
        Toast.info(res.msg, 2, () => {
          _this.setState({
            modal: false,
            isSubmit: true,
          });
        });
        return;
      }
      Toast.info(res.msg, 2, () => {
        _this.commodityview();
        _this.setState({
          modal: false,
          isSubmit: true,
        });
      });
    });
  };
  copyText = (e, event) => {
    const {
      localeStore: {
        locale: { ORDER },
      },
    } = this.props;
    event.stopPropagation();
    this.setState(
      {
        copyValue: e,
      },
      () => {
        let Url2 = document.getElementById("textCopy");
        Url2.select(); // 
        // Url2.focus();
        // Url2.blur();
        document.execCommand("Copy"); // 
        if (document.execCommand("Copy")) {
          Toast.info(ORDER.FZCG, 1);
        } else {
          if (window.webkit) {
            // window.webkit.messageHandlers.CopyText.postMessage(e);
            window.webkit.messageHandlers.CopyText.postMessage(e);
            Toast.info(ORDER.FZCG, 1);
          } else {
            Toast.info(ORDER.FZSB, 1);
          }
        }
      }
    );
  };

  showModal = (key) => (e) => {
    e.preventDefault(); //  Android \
    // console.log(key)

    this.setState({
      [key]: true,
    });
  };
  // toBase64 = (url) => {
  //   if(!url){
  //     return
  //   }
  //   let _this = this;
  //   let base64Data = "";
  //   imageToBase64(url, {
  //     mode: "no-cors",
  //   }) // Image URL
  //     .then((response) => {
  //       console.log(url.split(".").pop().toLowerCase())

  //       let type = url.split(".").pop().toLowerCase();
  //       base64Data = `data:image/${type};base64,` + response;
  //       // _this.setState({testImg:`data:image/${type};base64,`+response})
  //     })
  //     .catch((error) => {
  //       console.log(error); // Logs an error if there was one
  //     });
  //   return base64Data;
  // };

  onClose = (key) => () => {
    this.setState({
      [key]: false,
    });
  };
  canSubmit = () => {
    const {
      bank_name, //
      bank_user, //
      bank_number, //
      sell_price, //
    } = this.state;
    // return !!(bank_name && bank_user && bank_number && sell_price);
    return !!(bank_number && sell_price);
  };
  // 
  changePayType = (payType) => {
    this.setState({
      payType,
    });
  };
  // H5
  pingppH5 = (orderId) => {
    let _this = this;
    const { payType } = this.state;
    this.setState({ isSubmit: false });
    let channel = "";
    if (payType === "zfb") {
      channel = "alipay_wap";
    }
    if (payType === "wx") {
      channel = "wx_wap";
    }
    // const { userM4DStore, history } = this.props;
    CommodityApi.pingppcommission({
      p_id: getQueryString("id"),
      channel,
    }).then((res) => {
      this.setState({ isSubmit: true });

      if (res.status !== 1) {
        Toast.info(res.msg, 2, () => {
          _this.setState({
            modal: false,
            modal2: false,
          });
        });
        return;
      }
      console.log(res.data);
      let object = res.data;
      // return;
      pingpp.createPayment(object, function (result, err) {
        // object  Charge/Order/Recharge  JSON 
        //  alert  log
        //console.log(JSON.stringify(object));
        console.log("result:" + result);
        console.log("err.msg:" + err.msg);
        console.log("err.extra:" + err.extra);
        if (result == "success") {
          // JSAPI (wx_pub)wx_liteQQ  (qpay_pub)alipay_lite extra  URL
        } else if (result == "fail") {
          // Ping++  object JSAPI//QQ
        } else if (result == "cancel") {
          // JSAPIQQ 
        }
      });
      // window.location.href = `http://47.52.21.155:8200/api/commodity/chinapaysubmit?order_id=${res.data.orderId}`;
    });
  };

  // 
  getBankList = () => {
    let _this = this;
    const { chooseIndex } = this.state;
    const { userM4DStore } = this.props;
    CommodityApi.getBankList({
      open_id: userM4DStore.openid,
    }).then((res) => {
      if (res.status !== 1) {
        Toast.info(res.msg);
        return;
      }
      setTimeout(() => {
        _this.setState({
          bankList: res.data,
          bank_name: res.data[0].bankName, //
          bank_type: res.data[0].bankType, // 
          bank_user: res.data[0].bankUser, //
          bank_number: res.data[0].bankNumber, //
          code_img: res.data[0].codeImg, //
        });
      }, 500);
    });
  };
  getPayColor = (type) => {
    //
    switch (type) {
      case "":
        return {
          label: "",
          color: "#6DD400",
        };
      case "":
        return {
          label: "",
          color: "#0091FF",
        };
      case "":
        return {
          label: "",
          color: "#F7B500",
        };
      default:
        return {
          label: "",
          color: "#F7B500",
        };
    }
  };
  // 
  choosePayType = (index) => {
    const { bankList } = this.state;
    this.setState({
      chooseIndex: index,
      bank_name: bankList[index].bankName, //
      bank_type: bankList[index].bankType, // 
      bank_user: bankList[index].bankUser, //
      bank_number: bankList[index].bankNumber, //
      code_img: bankList[index].codeImg, //
    });
    console.log(index);
    console.log(bankList[index]);
  };

  render() {
    const { history } = this.props;
    const {
      localeStore: {
        locale: { WALLET },
        lang,
      },
    } = this.props;
    const {
      bank_name, //
      bank_user, //
      bank_number, //
      sell_price, //
      isSubmit,
      copyValue,
      modal,
      modal2,
      payType,
      cpData,
      testImg,
      base64Img,
      bankList,
      chooseIndex,
      Zhongduan,
    } = this.state;
    return (
      <div id="Fangwei">
        <AccountHeader />
        {/*  */}
        <textarea
          style={{ opacity: "0", position: "fixed", top: "-1000px" }}
          readOnly
          id="textCopy"
          value={copyValue}
        ></textarea>

        <div className="it_title"></div>
        <div className="goto-jilu">
          {/* <img src={ShareIcon} /> */}
          <ShareImg
            cpData={cpData}
            pName={cpData.pName}
            buyTime={formatDate(cpData.createTime)}
            // pFace={cpData.pFace}
            pFace={base64Img}
            nftId={cpData.ipfsNftInfoUrlHash}
            userName={cpData.userName}
            xiyou={formatZeroNumber(cpData.xiyou) + "%"}
            issuer={cpData.issuer}
            msg={""}
          />
        </div>

        <div
          onClick={() => {
            history.push("/CollectionDetail?id=" + getQueryString("id"));
          }}
          className="cp_card"
        >
          <div className="cp_top_box">
            <div className="cp_left">
              <div className="img_box">
                <img src={cpData.pFace} />
              </div>
            </div>
            <div className="cp_right">
              <h3>{cpData.pName}</h3>
              <div className="cp_tag">
                {(cpData.tags &&
                  formatTags(cpData.tags).map((i) => {
                    return <span key={i}>#{i}#</span>;
                  })) || <span>##</span>}
              </div>
              <div className="fxf">{cpData.issuer}</div>
            </div>
          </div>
        </div>

        {/*  */}
        <div className="cp_info_box">
          <div className="cp_info">
            <span></span>
            <span>{formatDate(cpData.createTime)}</span>
          </div>
          <div className="cp_info">
            <span></span>
            <span>{formatDate(cpData.buyTime)}</span>
          </div>
        </div>

        {/*  */}
        <div className="cp_detail_box">
          <h3></h3>
          <p>
            {cpData.mintHash}?a={cpData.nftId}
          </p>
          <h3></h3>
          <p
            onClick={(e) => {
              this.copyText(cpData.ipfsNftInfoUrlHash, e);
            }}
          >
            {cpData.ipfsNftInfoUrlHash || ""}
            <img src={fzIcon} />
          </p>
          <h3></h3>
          <p>{cpData.copyright || ""}</p>
          <h3></h3>
          <p
            dangerouslySetInnerHTML={{
              __html: cpData.remarks,
            }}
          ></p>
        </div>
        {/*  */}
        {cpData.sell === 0 && (
          <footer>
            <span onClick={this.showModal("modal")}></span>
            {/* <span
              onClick={() => {
                this.props.history.push({
                  pathname: "/Transfer",
                  state: { info: cpData },
                });
              }}
            >
              
            </span> */}
          </footer>
        )}
        {cpData.sell === 1 && (
          <footer>
            <span className="is_sell">
              {cpData.sellPrice}
              <div>
                {/* <i onClick={this.showModal("modal")}></i> */}
                <i
                  onClick={() => {
                    this.sellcancel();
                  }}
                >
                  
                </i>
              </div>
            </span>
          </footer>
        )}
        {cpData.sell === 2 && (
          <footer>
            <span className="is_sell">
              {cpData.sellPrice}
            </span>
          </footer>
        )}

        {/*  */}
        <Modal
          popup
          visible={modal}
          onClose={this.onClose("modal")}
          animationType="slide-up"
          // afterClose={() => {
          //   // alert("afterClose");
          // }}
        >
          <div className="myModal">
            <h1></h1>
            <div className="input_box">
              <h3></h3>
              <input
                onChange={(e) => this.onAmountChange(e, "sell_price")}
                value={sell_price}
                className="modal_input"
                type="number"
                placeholder={""}
              />
              <ul className="skfs">
                <h3></h3>

                {bankList.map((item, index) => {
                  return (
                    <li
                      className={(index === chooseIndex && "active_li") || ""}
                      key={item.id}
                      onClick={() => {
                        this.choosePayType(index);
                      }}
                    >
                      <i
                        style={{
                          backgroundColor: this.getPayColor(item.bankType)
                            .color,
                        }}
                      ></i>

                      <span>{item.bankType}</span>
                      <span>{cryptoAccountDisplay(item.bankNumber)}</span>
                    </li>
                  );
                })}
                <div
                  style={{ color: "white", padding: "2rem 0" }}
                  className="add_sk"
                  onClick={() => {
                    history.push("/AddPayment");
                  }}
                >
                  + 
                </div>
              </ul>

              {/* <h3>XXxx/</h3>
              <input
                onChange={(e) => this.onInputChange(e, "bank_name")}
                value={bank_name}
                className="modal_input"
                type="text"
                placeholder={""}
              />
              <h3></h3>
              <input
                onChange={(e) => this.onInputChange(e, "bank_number")}
                value={bank_number}
                className="modal_input"
                type="text"
                placeholder={""}
              />
              <h3></h3>
              <input
                onChange={(e) => this.onInputChange(e, "bank_user")}
                value={bank_user}
                className="modal_input"
                type="text"
                placeholder={""}
              /> */}
            </div>
            {/* <div className="md_tag">{cpData.shoucang}</div> */}
            <div className="md_tag">
              {" "}
              <i
                onClick={() => {
                  history.push("/Fengxian");
                }}
                style={{ color: "#E84848" }}
              >
                
              </i>
            </div>
            <div className="btn_box">
              <Button
                className="primary-button"
                onClick={() => {
                  this.setState({
                    modal: false,
                    modal2: true,
                  });
                  // this.sellcommodity();
                }}
                disabled={!this.canSubmit() || !isSubmit}
              >
                
              </Button>
            </div>
          </div>
        </Modal>

        {/*  */}
        <Modal
          popup
          visible={modal2}
          onClose={this.onClose("modal2")}
          animationType="slide-up"
          // afterClose={() => {
          //   // alert("afterClose");
          // }}
        >
          <div className="myModal">
            {/* <h4></h4> */}
            <div className="zf_num">{formatWalletPrice(sell_price / 10)}</div>
            <div className="zf_sm">10%</div>
            <div className="md_tag"></div>
            <ul className="zffs">
              <li
                onClick={() => {
                  // Toast.info("");
                  // return;
                  this.changePayType("wx");
                }}
                // className={(payType === "wx" && "active") || ""}
              >
                <span>
                  <img src={wxIcon} />
                  
                </span>

                {payType === "wx" && <Icon color={"#E32A2A"} type="check" />}
              </li>
              <li
                onClick={() => {
                  this.changePayType("zfb");
                }}
                // className={(payType === "zfb" && "active") || ""}
              >
                <span>
                  <img src={zfbIcon} />
                  
                </span>
                {payType === "zfb" && <Icon color={"#E32A2A"} type="check" />}
              </li>
            </ul>
            <div className="btn_box">
              <Button
                className="primary-button"
                disabled={!isSubmit}
                onClick={() => {
                  // this.buybox();
                  this.sellcommodity();
                }}
              >
                
              </Button>
            </div>
          </div>
        </Modal>
      </div>
    );
  }
}

export default Fangwei;
